.nav-mobile {
    display: inline-block;

    $goto-width: 50px;

    .nav-mobile__page-link,
    .nav-mobile__close-link,
    .nav-mobile__toggle-link {
        line-height: 1.5em;
        padding: 10px 15px;
        @include anchor("nav");
        vertical-align: baseline;
    }

    .nav-mobile__page-link {
        margin-right: $goto-width;
    }

    .nav-mobile__level--l0 > .nav-mobile__level__header > .nav-mobile__page-link {
        margin-left: $goto-width;
        text-align: center;
    }

    .nav-mobile__close-link,
    .nav-mobile__toggle-link {
        position: absolute;
        top: 0;
        height: 100%;

        span {
            font-size: 1.5em;
        }
    }

    .nav-mobile__close-link {
        left: 0;
    }

    .nav-mobile__toggle-link {
        right: 0;

        span {
            @extend .fa-plus;
        }
    }

    .nav-mobile__level.open > .nav-mobile__level__header .nav-mobile__toggle-link span {
        @extend .fa-minus;
    }

    $levels: 6;
    $level-colors: #0c0c0c #2c2c2c #4c4c4c #6c6c6c #8c8c8c #acacac #cccccc;
    @for $i from 0 through $levels {
        .nav-mobile__level--l#{$i} {
            @if ($i > 1) {
                margin-left: 15px;
            }

            $color: nth($level-colors, $i + 1);
            background-color: $color;
            color: contrasting-color($color);
        }
    }

    .nav-mobile__page-link--t2 {
        span {
            border-bottom: 2px $border-style-default $color-white;
            color: $color-white;
        }
    }

    &.nav-mobile--vertical--colors {
        $level-colors: (#222222 $color-white) ($color-white $color-black) (#00a2c2 $color-white) (#c9007d $color-white) (#ff6500 $color-white) (#87ca00 $color-white) (#cccccc $color-black);
        @for $i from 0 through $levels {
            .nav-mobile__level--l#{$i} {
                @if ($i > 1) {
                    margin-left: 15px;
                }

                $colors: nth($level-colors, $i + 1);
                background-color: nth($colors, 1);
                color: contrasting-color(nth($colors, 1));
                // color: nth($colors, 2);
            }
        }
    }

    .nav-mobile__level__header {
        position: relative;

    }

    .nav-mobile__level {
        ul:not([class]) {
            margin: 0;
        }

        li {
            border-bottom: $border-width-hairline $border-style-default #ebebeb;
            position: relative;
        }

        &.nav-mobile__level--left {
            right: 100%;
            left: -100%;
        }
        &.nav-mobile__level--center {
            right: 0;
            left: 0;
        }
        &.nav-mobile__level--right {
            right: -100%;
            left: 100%;
        }

        ul {
            display: none;
        }

        &.open > ul {
            display: block;
        }
    }
}

.nav-mobile__toggle,
.nav-mobile__content {
    display: none;
}

.nav-mobile__toggle-label {
    user-select: none;
}

.nav-mobile__toggle-label__icon {
    @extend .fa !optional;
    @extend .fa-bars !optional;
}

/** open state **/

.nav-mobile__toggle:checked + .nav-mobile__overlay {
    background-color: black;
    color: white;

    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    z-index: 1;

    .nav-mobile__toggle-label__icon {
        @extend .fa-close !optional;
    }

    .nav-mobile__content {
        display: block;
    }
}

/** scrolling inside menu support **/
.nav-mobile__overlay {
    overflow-y: auto;
}

@supports (display: flex) {

    .nav-mobile__overlay {
        overflow-y: hidden;
    }

    .nav-mobile__container {
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .nav-mobile__content  {
        flex: 1 1 auto;
        overflow-y: auto;
    }
}


/** no js **/

.no-js .nav-mobile__content:not(.nav-mobile__content--nojs) {
    display: none;
    flex: 0;
}
