////
/// Anchors
/// @group base
////

/**
 * If you need to add a new link style, modify an existing link style, or
 * extend an existing style, please do so at the bottom of this file. This is
 * where all of our anchor and button styles live.
 * Adding, modifying, or extending styles requires you follow process:
 *   1. Establish full set of styles
 *   2. Present to the Web Team at the appropriate time during the weekly Code
 *      Review. A decision will be made on how the new styles will be
 *      implemented.
 *   3. Push your changes and celebrate!
 */

a {
    @include anchor(inline);

    // hide extra content, text targetted only to screen readers
    & > span:not([class]):not([itemprop]) {
        @include visually-hidden;
    }
}

%heading-elements > a {
    @include anchor(heading);
}

figure > a {
    @include anchor(block);
}

.card {
    @include anchor(card);
}

.cta {
    @include anchor(cta);
}
