////
/// Tabs
/// @group components
////

.js {
    .tabs__content__heading {
        &, &.inactive + div {
            @include visually-hidden;
        }
    }
}

/** Layout code: manages content width for tab-menu and tab-content */
.tabs--top-level-layout {
    @extend .fallout;
    @extend .fallout--both;

    .tabs__menu {
        @extend .wide-column;
    }
}


.tabs__menu {
    margin-top: $measure-large;
    margin-bottom: $measure-medium;
}

.tabs--inline .tabs__menu__item {
    @include anchor(activable-inline);
}

.tabs--block .tabs__menu__item {
    @include anchor(activable);
}
