
.cbl--3 {
    .cbl__ctas {
        @include grid-column-map(
                        (
                                "small": 12,
                                "medium": 6
                        )
        );

        @include grid-column-push-map(
                        (
                                "small": 0
                        )
        );

        align-self: center;

        a {
            @include anchor(cta);
            @include anchor(inline-block);

            float: left;
            clear: left;
            margin-bottom: $measure-small;
        }
    }

    .cbl__keyinfo {
        @extend .wide-column;

        .key-info {
            @extend %grid--quarters;
        }

        .key-info__prop {
            font-weight: 600;
        }
        .key-info__value {
            margin: 0 0 $measure-medium 0;
        }
    }
}

.cbl--2 {
    .cbl__keyinfo {
        @include grid-column-map(
                        (
                                "small": 12,
                                "medium": 6
                        )
        );

        order: 2;

        .key-info {
            @extend %grid--thirds;
        }

        .key-info__prop {
            font-weight: 600;
        }
        .key-info__value {
            margin: 0 0 $measure-medium 0;
        }
    }

    .cbl__ctas {
        @extend .fallout-column;

        order: 3;

        text-align: center;
        align-self: center;

        a {
            @include anchor(cta);
            @include anchor(inline-block);
        }
    }

}

.cbl--1 {
    .cbl__ctas {
        @include grid-column-map(
                        (
                                "small": 12,
                                "medium": 6
                        )
        );

        @include grid-column-push-map(
                        (
                                "small": 0
                        )
        );

        text-align: center;
        align-self: center;

        a {
            @include anchor(cta);
            @include anchor(inline-block);
        }
    }

    .cbl__keyinfo {
        @include grid-column(12);
        //@extend %grid__12;

        .key-info {
            @extend %grid;

            > div {
                @include grid-column-map(
                                (
                                        "small": 4,
                                        "medium": 2
                                )
                );

                @include grid-column-push-map(
                                (
                                        "small": 0,
                                        "medium": 1
                                )
                );
            }
        }

        .key-info__prop {
            font-weight: 600;
        }
        .key-info__value {
            margin: 0 0 $measure-medium 0;
        }
    }
}

.cbl__title {
    @include grid-column-map(
                    (
                            "small": 12,
                            "medium": 6
                    )
    );

    @include grid-column-push-map(
                    (
                            "small": 0
                    )
    );
}

.cbl__ctas {
}

.cbl__keyinfo {
}
