////
/// Tooltips
/// @group components
////


[data-tooltip] {
    position: relative;
    border-bottom: $border-width-hairline $border-style-alternate currentColor;

    &::after {
        @include transition(opacity);
        content: attr(data-tooltip);
        opacity: 0;
        background-color: $color-white;
        color: $color-black;
        padding: $measure-small;
        display: block;
        border: $border-width-hairline $border-style-default $color-black;
        position: absolute;
        left: 100%;
        bottom: 100%;
    }

    &:hover::after {
        opacity: 1;
    }
}
