////
/// Media
/// @group components
////


.media,
.video-preview-wrapper {
    width: 100%;
    height: auto;
    margin-bottom: $measure-medium;
    position: relative;
    overflow: hidden;

    // by default, assume 16:9
    &:not([class*="media--"]) {
        @extend .media--16-9;
    }
}

.video-preview-wrapper--loading {
    %media-elements {
        @include visually-hidden;
    }
}

.video-preview {
    margin-bottom: $measure-medium;
}

.media %media-elements,
.video-preview-wrapper .video-preview {
    // necessary for `iframe`
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin-bottom: 0;
}

.media--vimeo,
.media--youtube {
    @extend .media--16-9;
}

// Build aspect ratio classes
@each $ratio, $percentage in $ratios {
    .img--#{nth($ratio, 1)}-#{nth($ratio, 2)} {
        height: $percentage;
    }
    .media--#{nth($ratio, 1)}-#{nth($ratio, 2)} {
        padding-top: $percentage;
    }
}
