////
/// Tabs
/// @group components
////

.js {
    .tabs__content__heading {
        &, &.inactive + div {
            @include visually-hidden;
        }
    }
}

.linked-content--top-level-layout {
    @extend .fallout;
    @extend .fallout--right;

    .linked-content__menu-wrapper {
        @include grid-column-map(
                        (
                                "small": 4,
                                "medium": 3
                        )
        );
    }
}

.linked-content__menu__item {
    @include anchor('activable');
}

.linked-content__menu-wrapper,
.linked-content__menu {
    position: relative;
}
