////
/// Anchors Mixins
/// @group abstracts
////

/**
 * Managing anchors is a difficult process on a website as vast as ours, and
 * given the great variety in styles our anchors take, it is of the utmost
 * importance that they be set up properly and with extensibility on the mind.
 * The current issue we face is that we are not making proper use of the
 * cascade. New styles for links are introduced in a bespoke manner, and due to
 * the way these different designs and patterns are communicated, or lack
 * thereof, we even have instances where the same stylistic goal is being
 * accomplished in two or more separate instances of CSS. To properly use the
 * cascade, we need to dissect the variety of different styles we currently
 * use, figure out any common denominators and determine how each style
 * branches away from the default element’s styles—exposing how each style
 * cascade and iterate on the established patterns.
 *
 * The biggest challenge in doing this is understanding the scope of the links
 * and how far-reaching any high-level styles will cascade, and mentally
 * extrapolating to understand how the styles will be managed and maintained,
 * and ensuring that every step of the way takes us closer to unity and
 * simplicity.
 *
 *
 * It is important to make the details of the anchor tag, its states, and its
 * functionality and known and understood completely:
 *
 * :link
 *   - Applies to any `<a>` tag with a `href` attribute, whether empty or not
 *   - Persists regardless of other states based on above criteria
 * :visited
 *   - Applies to any `<a>` tag with a `href` in the browser history
 *   - Applies to any `<a>` tag with a target anchor which has been activated
 *   - Restricted to styling `color`, `background-color`, `border-color` and
 *     variants, `outline-color`, and `fill` and `stroke` color values for
 *     security/privacy reasons
 *   - Similarly to above, checking for these properties with JS will yield
 *     results as if `:visited` anchors are just `:link` anchors
 * :hover
 *   - Applies to any `<a>` tag which the user designates with a pointing
 *     device but does not necessarily activate it
 * :focus
 *   - Applies to any `<a>` tag which has been activated by the user, either
 *     from selecting with they keyboard or pointing device
 * :active
 *   - Applies to any `<a>` tag which is *currently* being activated by the
 *     user (i.e. mouse button down)
 *
 *
 * Order of states for a link when it is moused over and clicked:
 *
 * :link -> :link:hover -> :link:hover:focus -> :link:visited:hover:active
 *  N/A  ->  Mouse Over ->  Mouse Down       ->  Mouse Up
 *
 * For this reason, it’s important to maintain an order of states in the CSS,
 * easily memorised with the phrase "LoVe HAte":
 *   - :link
 *   - :visited
 *   - :hover
 *   - :focus
 *   - :active
 *
 *
 * The only properties which live inside the `:link`, `:visited`, `:hover`,
 * `:focus`, and `:active` pseudo classes are as follow:
 *   - background-color
 *   - color
 *   - border-bottom-width
 *   - border-bottom-style
 *   - border-bottom-color
 *   - outline
 *   - text-decoration
 *
 *
 * It is important to take notice of some other key points regarding anchor
 * states:
 *   - `:focus` and `:active` states do not automatically trigger `:hover`;
 *     this means styles that should apply to all three must be declared for
 *     all three
 *
 *
 * If a link should *not* appear visually-similar to the default anchor styles,
 * there are a number of properties/values that need to be reset/unset which
 * cascade and need to be anticipated.
 *   1. Set the `border-border-width` to `0` (supercedes `border-bottom-color`
 *      and `border-bottom-style`)
 *   2. Set the `background-color` to `transparent` for `:focus` and `:active`
 *      states
 */

///
/// Anchor!
///
@mixin anchor($type) {
    @if not $type {
        @error "anchor() expects an Anchor Type.";
    }

    @if ($type == "default") or ($type == "inline") {
        border-bottom-width: $border-width-hairline;
        border-bottom-style: $border-style-default;
        position: relative;
        text-decoration: none;

        &:link {
            color: $color-endeavour;
            border-bottom-color: currentColor;
        }

        &:visited {
            color: $color-daisy;
        }

        &:hover,
        &:focus {
            color: mix($color-black, $color-endeavour, 10%);
            border-bottom-width: $border-width-default;
        }

        &:focus {
            outline: $border-width-hairline $border-style-alternate mix($color-black, $color-endeavour, 10%);
        }

        &:active {
            background-color: mix($color-black, $color-endeavour, 10%);
            color: contrasting-color(mix($color-black, $color-endeavour, 10%));
            border-bottom-width: $border-width-default;
            border-bottom-color: mix($color-black, $color-endeavour, 10%);
        }
    } @else if ($type == "reset") {
        border-bottom-width: 0;

        &:link,
        &:visited,
        &:hover,
        &:focus,
        &:active {
            color: inherit;
        }
    } @else if ($type == "block") {
        @include anchor(reset);
        display: block;
    } @else if ($type == "inline-block") {
        @include anchor(reset);
        display: inline-block;
    } @else if ($type == "heading") {
        border-bottom-width: $border-width-default;
    } @else if ($type == "card") {
        @include anchor(inline-block);

        &:link,
        &:visited {
            background-color: $color-white;
            color: $color-black;
        }

        .card__title {
            color: $color-endeavour;
        }
    } @else if ($type == "cta") {
        @include anchor(block);

        padding: $measure-medium;

        &:link,
        &:visited {
            color: $color-white;
            background-color: $color-gallery;
        }

        &:hover,
        &active,
        &:focus {
            color: $color-white;
            background-color: $color-quill;
        }
    } @else if ($type == "nav") {
        @include anchor(block);

        border: none;

    } @else if ($type == 'activable') {
        @include anchor(block);
        @include anchor(activable-base);
    } @else if ($type == 'activable-inline') {
        @include anchor(inline-block);
        @include anchor(activable-base);
    } @else if ($type == "activable-base") {
        @include transition;
        background-image: linear-gradient(to top,
                                          $color-quill 0%,
                                          $color-quill $border-width-thick,
                                          $transparent $border-width-thick,
                                          $transparent 100%);
        color: $color-emperor;
        border-bottom-width: 0;
        position: relative;
        font-weight: $font-weight-bold;
        line-height: 1;
        text-decoration: none;

        @include media("below:medium") {
            padding: $measure-small $measure-medium calc(#{$measure-small} + #{$border-width-thick});
        }

        @include media("above:medium") {
            padding: $measure-small $measure-medium calc(#{$measure-medium} + #{$border-width-thick});
        }

        &::before {
            @include transition(transform 0.4s, opacity 0.2s);
            content: "";
            opacity: opacity("tint");
            background-color: $color-black;
            width: 100%;
            height: $border-width-thick;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%) scaleX(0);
        }

        &:hover,
        &:focus {
            color: $color-black;
        }

        &:focus {
            outline: $border-width-hairline $border-style-alternate $color-black;
        }

        &:active {
            background-color: $color-quill;
            color: $color-black;
        }

        &[aria-selected="true"] {
            background-color: $color-gallery;
            color: $color-black;

            &::before {
                opacity: 1;
                transform: translateX(-50%) scaleX(1);
            }
        }
    }
}

///
/// Inline Anchor
///
@mixin define-inline-anchor($link-color: $color-endeavour,
                            $visited-color: $color-daisy,
                            $pre-action-color: $color-curious,
                            $action-color: $color-powder) {
    & > a {
        &,
        &:link {
            color: $link-color;
            border-bottom: $border-width-hairline $border-style-default $link-color;
        }

        &:visited {
            color: $visited-color;
            border-bottom-color: $visited-color;
        }

        &:hover,
        &:focus {
            color: $pre-action-color;
            border-bottom-color: $pre-action-color;
        }

        &:active {
            background-color: $transparent;
            color: $action-color;
            border-bottom-width: $border-width-default;
            border-bottom-color: $action-color;
        }
    }
}

@mixin define-block-anchor() {
    display: block;
    border-bottom-width: 0;
    @content;
}

@mixin define-block-text-anchor($text-color, $text-bg, $interact-color, $interact-bg, $text-element: "*") {
    @include define-block-anchor() {
        &:link,
        &:visited {
            color: $text-color;
            background-color: $text-bg;
        }

        &:hover,
        &:focus,
        &:active {
            color: $interact-color;
            background-color: $interact-bg;
        }

        @content;
    }
}

