
.usp-strip {
    @extend %grid;
    margin-top: 2 * $measure-large;
}

.usp-strip--n1 {
}

.usp-strip--n2 > div {
    @include grid-column-map(
                    (
                            "small": 4
                    )
    );

    @include grid-column-push-map(
                    (
                            "small": 3
                    )
    );


    &:first-child {
        @include grid-column-push-map(
                        (
                                "small": 1
                        )
        );
    }


}

.usp-strip--n3 {
    @extend %grid--thirds;
}

.usp-strip__item {
    text-align: center;
    margin-bottom: 2 * $measure-large;

    @include font-family(meta);

}

.usp-strip__item__value {
    font-size: 2em;
    font-weight: 300;
}
