////
/// Accordion
/// @group components
////

.accordion--top-level-layout {
    @extend .fallout;
    @extend .fallout--both;
}

.js {
    .accordion__content__heading.inactive + div {
        @include visually-hidden;
    }
}

.accordion__content__heading__icon {
    @extend .fa;
    float: right;

    .accordion__content__heading.inactive & {
        @extend .fa-plus;
    }

    .accordion__content__heading.active & {
        @extend .fa-minus;
    }
}

.accordion__content__heading {
    @include anchor('activable');

    h2 {
        margin: 0;
    }
}
